.toggle,
[id^=drop] {
  display: none;
}
/* Giving a background-color to the nav container. */
nav { 
  margin:0;
  padding: 0;
  background-color: rgba(255,255,255,1);
  border: 0.5px rgba(130, 130, 130, 1);
  box-shadow: -3px 4px 20px 1px rgb(128 128 128 / 33%);
  font-family: "Poppins", sans-serif;
  height: 12vh;
}

#logo {
  display: block;
  padding: 0 30px;
  float: left;
  font-size:20px;
  line-height: 60px;
}

/* Since we'll have the "ul li" "float:left"
 * we need to add a clear after the container. */

nav:after {
  content:"";
  display:table;
  clear:both;
}

/* Removing padding, margin and "list-style" from the "ul",
 * and adding "position:reltive" */
nav ul {
  float: right;
  padding:0;
  margin:0;
  list-style: none;
  position: relative;
  }
  
/* Positioning the navigation items inline */
nav ul li {
  margin: 0px;
  display:inline-block;
  float: left;
  background-color: rgba(255,255,255,1);
  color: black;
  }

/* Styling the links */
nav p {
  display:block;
  padding:8px 10px;  
  color:black;
  font-size:2.2vh;
  text-decoration:none;
}


nav ul li ul li:hover { background: #000000; }

/* Background color change on Hover */
nav p:hover { 
  background-color: rgba(255,255,255,1); 
  border-radius: 50%;
}

/* Hide Dropdowns by Default
 * and giving it a position of absolute */
nav ul ul {
  display: none;
  position: absolute; 
  /* has to be the same number as the "line-height" of "nav a" */
  top: 60px; 
}
  
/* Display Dropdowns on Hover */
nav ul li:hover > ul {
  display:inherit;
}
  
/* Fisrt Tier Dropdown */
nav ul ul li {
  width:170px;
  float:none;
  display:list-item;
  position: relative;
}

/* Second, Third and more Tiers 
 * We move the 2nd and 3rd etc tier dropdowns to the left
 * by the amount of the width of the first tier.
*/
nav ul ul ul li {
  position: relative;
  top:-60px;
  /* has to be the same number as the "width" of "nav ul ul li" */ 
  left:170px; 
}

  
/* Change ' +' in order to change the Dropdown symbol */
li > p:after { content:  ' +'; }
li > p:only-child:after { content: ''; }


/* Media Queries
--------------------------------------------- */

@media all and (max-width : 768px) {

  #logo {
    display: block;
    padding: 0;
    width: 100%;
    text-align: center;
    float: none;
  }

  nav {
    margin: 0;
  }

  /* Hide the navigation menu by default */
  /* Also hide the  */
  .toggle + p,
  .menu {
    display: none;
      background: rgba(255,255,255,1);

  }

  /* Stylinf the toggle lable */
  .toggle {
    position: absolute;
    top: 0px;
    right: 0px;
    display: block;
     background-color: rgba(255,255,255,1); 
    padding:14px 20px;  
    color:#000000;
    font-size:17px;
    text-decoration:none;
    border:none;
  }

  .toggle:hover {
    background-color: grey;
  }

  /* Display Dropdown when clicked on Parent Lable */
  [id^=drop]:checked + ul {
    display: block;
  }

  /* Change menu item's width to 100% */
  nav ul li {
    display: block;
    width: 100%;
    }

  nav ul ul .toggle,
  nav ul ul p {
    padding: 0 40px;
  }

  nav ul ul ul p {
    padding: 0 80px;
  }

  nav p:hover,
  nav ul ul ul a {
    background-color: #00000017;
  }
  
  nav ul li ul li .toggle,
  nav ul ul p,
  nav ul ul ul p{
    padding:14px 20px;  
    color:#FFF;
    font-size:17px; 
  }
  
  
  nav ul li ul li .toggle,
  nav ul ul p {
    background-color: rgba(255,255,255,1); 
  }

  /* Hide Dropdowns by Default */
  nav ul ul {
    float: none;
    position:static;
    color: #ffffff;
    /* has to be the same number as the "line-height" of "nav a" */
  }
    
  /* Hide menus on hover */
  nav ul ul li:hover > ul,
  nav ul li:hover > ul {
    display: none;
  }
    
  /* Fisrt Tier Dropdown */
  nav ul ul li {
    display: block;
    width: 100%;
  }

  nav ul ul ul li {
    position: static;
    /* has to be the same number as the "width" of "nav ul ul li" */ 

  }

}

@media all and (max-width : 330px) {

  nav ul li {
    display:block;
    width: 94%;
  }

